import React from 'react'
import PropTypes from 'prop-types'

import pic01 from '../images/banner.446ff169.jpg'
import pic02 from '../images/web banner-01.5ac9bad2.jpg'
import pic03 from '../images/web banner-01.95e92e77.jpg'

class Main extends React.Component {
  render() {

    let close = <div className="close" onClick={() => { this.props.onCloseArticle() }}></div>

    return (
      <div ref={this.props.setWrapperRef} id="main" style={this.props.timeout ? { display: 'flex' } : { display: 'none' }}>

        <article id="Rose2018Volunteer" className={`${this.props.article === 'Rose2018Volunteer' ? 'active' : ''} ${this.props.articleTimeout ? 'timeout' : ''}`} style={{ display: 'none' }}>
          <h2 className="major">SJS Rose2018 Volunteer</h2>
          <span className="image main"><img src={pic01} alt="" /></span>
          <p>Volunteer registration site for charity sale organized by St. James Settlement</p>
          <a href="https://event.sjs.org.hk/Rose2018Volunteer/" rel="noopener noreferrer" target="_blank">Link</a>
          {close}
        </article>

        <article id="GORun2018" className={`${this.props.article === 'GORun2018' ? 'active' : ''} ${this.props.articleTimeout ? 'timeout' : ''}`} style={{ display: 'none' }}>
          <h2 className="major">SJS GORun2018</h2>
          <span className="image main"><img src={pic02} alt="" /></span>
          <p>Event site for charity run organized by St. James Settlement</p>
          <a href="https://event.sjs.org.hk/gorun2018/" rel="noopener noreferrer" target="_blank">Link</a>          {close}
        </article>

        <article id="SOBH2018" className={`${this.props.article === 'SOBH2018' ? 'active' : ''} ${this.props.articleTimeout ? 'timeout' : ''}`} style={{ display: 'none' }}>
        <h2 className="major">SJS SOBH2018</h2>
        <span className="image main"><img src={pic03} alt="" /></span>
        <p>Event site for charity orienteering organized by St. James Settlement</p>
        <a href="https://event.sjs.org.hk/sobh2018/" rel="noopener noreferrer" target="_blank">Link</a>          {close}
        </article>



      </div>
    )
  }
}

Main.propTypes = {
  route: PropTypes.object,
  article: PropTypes.string,
  articleTimeout: PropTypes.bool,
  onCloseArticle: PropTypes.func,
  timeout: PropTypes.bool,
  setWrapperRef: PropTypes.func.isRequired,
}

export default Main