import React from 'react'
import PropTypes from 'prop-types'

const Header = (props) => (
    <header id="header" style={props.timeout ? {display: 'none'} : {}}>
        <div className="logo">
            <span className="icon fa-code"></span>
        </div>
        <div className="content">
            <div className="inner">
                <h1>Bosco O.C. Fong</h1>
                <p>A Web Developer.</p>
            </div>
        </div>
        <div className="content">
            <div className="inner">
                <h2>About Me</h2>
                <p>Languages I speak: JavaScript</p>
                <p>Things I enjoy:  Building stuff for people. Spreading ideas through browsers.</p>
                
            </div>
        </div>
        <div className="content">
            <div className="inner">
                <h2>My Recent Works</h2>
               
            </div>
        </div>
        <nav>
            <ul>
                <li><a href="javascript:;" onClick={() => {props.onOpenArticle('Rose2018Volunteer')}}>SJS Rose2018 Volunteer</a></li>
                <li><a href="javascript:;" onClick={() => {props.onOpenArticle('GORun2018')}}>SJS GORun2018</a></li>
                <li><a href="javascript:;" onClick={() => {props.onOpenArticle('SOBH2018')}}>SJS SOBH2018</a></li>
            </ul>
        </nav>
    </header>
)

Header.propTypes = {
    onOpenArticle: PropTypes.func,
    timeout: PropTypes.bool
}

export default Header
